<template>
    <div class="one-logistics">
        <tab-two @handelTab="handelTab"></tab-two>
        <div class="public-search-headjz">
            <search-status :status-list="auditStatusList" @auditStatus="auditStatus"></search-status>
            <Divider style="margin:12px 0" />
            <div class="public-search-input-jz">
                <Row :gutter="40" class="publc-bottom20">
                    <Col span="4">
                        <span>启运地：</span>
                        <Cascader
                            :data="cityData"
                            clearable
                            filterable
                            placeholder="国家/省份/城市"
                            @on-change="handelDirStart"
                        ></Cascader>
                    </Col>
                    <Col span="4">
                        <span>选择场所：</span>
                        <Select v-model="searchForm.departurePort" filterable clearable>
                            <Option
                                v-for="item in departurePortList"
                                :value="item.value"
                                :key="item.value"
                            >{{ item.label }}</Option>
                        </Select>
                    </Col>
                    <Col span="4">
                        <span>目的地：</span>
                        <Cascader :data="cityData" clearable filterable @on-change="handelDirEnd"></Cascader>
                    </Col>
                    <Col span="4">
                        <span>选择场所：</span>
                        <Select v-model="searchForm.destinationPort" filterable>
                            <Option
                                v-for="item in destinationPortList"
                                :value="item.value"
                                :key="item.value"
                            >{{ item.label }}</Option>
                        </Select>
                    </Col>
                    <Col span="4">
                        <span>中转地：</span>
                        <Cascader :data="cityData" clearable filterable @on-change="handelDirMid"></Cascader>
                    </Col>
                    <Col span="4">
                        <span>选择场所：</span>
                        <Select v-model="searchForm.trsspmPort" filterable>
                            <Option
                                v-for="item in trsspmPortList"
                                :value="item.value"
                                :key="item.value"
                            >{{ item.label }}</Option>
                        </Select>
                    </Col>
                </Row>
                <Row :gutter="40" class="publc-bottom20">
                    <Col span="4">
                        <span @click="hh()">合同编号：</span>
                        <Input v-model="searchForm.contractCode" clearable></Input>
                    </Col>
                    <Col span="4">
                        <span>运输类型：</span>
                        <Select v-model="searchForm.trspMode" filterable clearable>
                            <Option
                                v-for="item in trspModeList"
                                :value="item.label"
                                :key="item.value"
                            >{{ item.label }}</Option>
                        </Select>
                    </Col>
                    <Col span="4">
                        <span>货物类型：</span>
                        <Select v-model="searchForm.cargoType" filterable clearable>
                            <Option
                                v-for="item in cargoTypeList"
                                :value="item.label"
                                :key="item.value"
                            >{{ item.label }}</Option>
                        </Select>
                    </Col>
                </Row>
            </div>
            <Divider style="margin:12px 0" />
            <div class="search-btn">
                <Button class="searchButton" shape="circle" @click="handleFilter_m">
                    查询
                    <Icon type="ios-search" />
                </Button>
            </div>
        </div>

        <div class="public-btn-pagina">
            <div></div>
            <pagination :total="total" :page="searchForm.page" @changePage="changePage_m"></pagination>
        </div>

        <div class="public-component-line"></div>
        <table-cart :tableList="listData"></table-cart>
    </div>
</template>

<script>
import searchStatus from '../order/search-status/index'
import pagination from '../order/pagination/index'
import tableCart from './table-cart/index'
import {
    apiGetRoleCstmEntrustList,
    apiCityDic,
    apiChangJing,
} from '@/api/logisticsCommission'
import lineHeader from './line-header/index'
import tabTwo from './tab-two/index'
import tableMixin from '@/mixins/tableMixin'
import { apiDictionaryQueryByCacheTypeKey } from '@/api/dictionary'
import { mapGetters } from 'vuex'
export default {
    mixins:[tableMixin],
    components: {
        lineHeader,
        tabTwo,
        searchStatus,
        pagination,
        tableCart,
    },
    data() {
        return {
            // 分页的搜索条件
            searchForm: {
                page: 1,
            },
            total: 0,
            // 表格的数据
            listData: [],
            // 状态 后期会取值字典
            auditStatusList: [
                { label: '已下单', value: '已下单' },
                { label: '已接受', value: '已接受' },
                { label: '已拒绝', value: '已拒绝' },
                { label: '已取消', value: '已取消' },
                { label: '未确定计划', value: '未确定计划' },
                { label: '不同意计划', value: '不同意计划' },
                { label: '待货好', value: '待货好' },
                { label: '进行中', value: '进行中' },
                { label: '已完成', value: '已完成' },
                { label: '异常进行中', value: '异常进行中' },
                { label: '异常处结束', value: '异常处结束' },
                { label: '已撤销', value: '已撤销' },
                { label: '已支付', value: '已支付' },
            ],
            // 联集选择器
            cityData: [],
            //启运地场所集合
            departurePortList: [],
            //目的地场所集合
            destinationPortList: [],
            //中转地场所集合
            trsspmPortList: [],
            // 运输类型
            trspModeList: [],
            // 货物类型
            cargoTypeList: [],
        }
    },
    computed: {},
    created() {
        this.initDic()
    },
    methods: {
        auditStatus(code) {
            // 控制状态切换传值
            this.searchForm.entrustExecutoryStatus = code
        },
        // tab的切换
        handelTab(path) {
            this.$router.push(path)
        },
        hh() {
            this.$router.push('/logisstics-commission/add-single')
        },
        // 直接接口
        getTableList() {
            let data = {
                ...this.searchForm,
                entrustType: 0, // 0/1   单次 / 周期
            }
            apiGetRoleCstmEntrustList({ data }).then(({ data }) => {
                let { content } = data
                let arr = (content[0].res || []).map((item) => {
                    return {
                        ...item,
                        location: item.getSchemePathListRes,
                    }
                })
                this.listData = arr
                this.total = content[0].totalElements
            })
        },
        // 初始化字典数据
        initDic() {
            // 运输类型
            apiDictionaryQueryByCacheTypeKey({
                dictionaryTypeKey: 'TRANSPORT_TYPE',
            }).then(({ data }) => {
                let newData = data.map((item) => {
                    return {
                        value: item.dictionaryKey,
                        label: item.dictionaryValue,
                    }
                })
                this.trspModeList = newData
            })
            // 货物类型
            apiDictionaryQueryByCacheTypeKey({
                dictionaryTypeKey: 'GOODS_TYPE',
            }).then(({ data }) => {
                let newData = data.map((item) => {
                    return {
                        value: item.dictionaryKey,
                        label: item.dictionaryValue,
                    }
                })
                this.cargoTypeList = newData
            })
            //城市三联
            apiCityDic().then(({ data }) => {
                this.cityData = this._processDr(data)
            })
        },
        // 拿到启运地的场景字典
        async handelDirStart(data) {
            let arr = await this._handelDir(data.value)
            this.departurePortList = arr
        },
        // 拿到目的地的场景字典
        async handelDirEnd(val, data) {
            let arr = await this._handelDir(val, data)
            this.destinationPortList = arr
        },
        // 中转地的场景字典
        async handelDirMid(val, data) {
            let arr = await this._handelDir(val, data)
            this.trsspmPortList = arr
        },
        //拿到场景的字典
        _handelDir(val, data) {
            return new Promise((reslove, rejact) => {
                let code = val[val.length - 1]
                let iscode = val[val.length - 2]
                if (
                    iscode == '110000000000' ||
                    iscode == '120000000000' ||
                    iscode == '500000000000' ||
                    iscode == '310000000000'
                ) {
                    code = iscode
                }
                apiChangJing({ city: code }).then(({ data }) => {
                    let arr = data[0]
                    let newArr = arr.map((item) => {
                        return {
                            value: item.sttnCode,
                            label: item.sttnName,
                        }
                    })
                    reslove(newArr)
                })
            })
        },
        // 处理练级城市
        _processDr(arr) {
            let setChildren = (child) => {
                child.forEach((item) => {
                    if (item.child) {
                        item.value = item.addressCode
                        item.label = item.addressName
                        item.children = item.child
                        delete item.child
                        setChildren(item.children)
                    }
                    if (!item.child) {
                        delete item.child
                    }
                })
            }
            setChildren(arr)
            arr.forEach((item) =>
                item.children.forEach((item) =>
                    item.children.forEach((item) => delete item.children)
                )
            )
            return arr
        },
    },
}
</script>

<style lang="scss" scoped>
.one-logistics {
}
</style>
